.Addtitle {
    width: 100%;
    display: flex;
    color: #000000;
    justify-content: center;
}

.Addtitle h2 {
    margin: 10px !important;
}

.AddMainContainer {
    display: flex;
    width: 100%;
    justify-content: space-evenly;
    align-items: center;
}
.AddFromContainer {
    display: flex;
    justify-content: flex-start;
}

.AddBox {
    width: 400px;
    height: 400px;
    padding-top: 15px !important;
    background-color: rgba(255, 255, 255, 0.8) !important;
    border-radius: 9px !important;
}

.AddForm {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    align-items: center;
    justify-content: center;
}

.AddInput {
    width: 75%;
    margin-top: 15px !important;
}

.AddButtonContainer {
    width: 100%;
    height: 100%;
    /* border: 1px solid black; */
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    margin-top: 5px;
    align-items: center;
}

.AddButton {
    background-color: rgb(118, 227, 247) !important;
    margin-top: 10px !important;
}

.AlertContainer {
    display: flex;
    width: 100%;
    height: 65px;
}

.DisplayAddVacationCard {
    height: 250px;
    width: 400px;
    margin-top: 25px;
    border: 2px rgb(65, 141, 155) solid;
    margin-left: 10px;
    margin-right: 10px;
}

.DisplayAddVacationCardImage {
    width: 100%;
    height: 140px;
    border-bottom: 1px rgb(65, 141, 155) solid;
}

@media only screen and (max-width: 850px) {
    .AddMainContainer {
        display: flex;
        width: 100%;
        /* justify-content: space-evenly; */
        align-items: center;
        flex-direction: column-reverse;
    }
    .DisplayAddVacationCard {
        margin-bottom: 20px;
    }
}
