@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@100&display=swap');


.PaperContainer {
    height: 50vh;
    width: 50vh;
    border: forestgreen 1px solid;
}
.LoginTEXT {
    text-align: center;
    color: #000000;
    font-family: "Fira Sans Extra Condensed", sans-serif;
}
.LoginTEXT h1 {
    font-family: 'Roboto', sans-serif;
    font-size: 2.5rem;
}

.Alert {
    width: 50%;
    margin: 10px auto;
}

.LoginContainer {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
}
.LoginBox {
    width: 400px;
    height: 220px;
    background-color: rgba(255, 255, 255, 0.8) !important;
    border-radius: 9px !important;
}

.LoginForm {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    align-items: center;
    justify-content: center;
}
.LoginForm .LoginInput {
    width: 80%;
    margin: 5px;
    /* border: 1px #000 solid !important; */
}
.LoginButtonContainer {
    width: 100%;
    /* border: 1px solid black; */
    display: flex;
    justify-content: space-evenly;
    margin-top: 20px;

}

.LoginButtonContainer span {
    font-weight: bold;
    font-family: 'Roboto', sans-serif;
}

.LoginButtonContainer .Button {
    background-color: rgb(118, 227, 247) !important;
}
