.AddVacationContainer{
    width: 400px;
    height: 260px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 15px;
    padding-left: 10px;
    padding-right: 10px;
}

.Main {
    margin-top: 22px;
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.Main:hover {
    background-color: rgb(214, 245, 242);
}

.ControlPointIcon {
    font-size: 50px !important;
    color: rgb(79, 131, 155) !important;
}


@media only screen and (max-width: 1125px) {
    .AddVacationContainer{
        width: 43vw;
    }
}

@media only screen and (max-width: 674px) {
    .AddVacationContainer{
        width: 80vw;
    }
  }