.Card {
    width: 400px;
    margin-top: 25px;
    /* border: 2px forestgreen solid; */
    margin-left: 10px;
    margin-right: 10px;
}
.CardImage {
    width: 100%;
    height: 140px;
}

.CardImage:hover {
    -webkit-transform: scale(1.2);
    -ms-transform: scale(1.2);
    transform: scale(1.2);
    transition: 1s ease;
    text-align: center;
    /* height: 100%; */
}


.MuiCardContent-root {
    padding-bottom: 5px !important;
}

.CardContent {
    display: flex;
    flex-direction: column;
    /* padding: 10px 0 0 20px !important; */
}

.VacationName {
    font-size: 27px;
    font-weight: bold;
}

@media only screen and (max-width: 1125px) {
    .Card {
        width: 43vw;
    }
}

@media only screen and (max-width: 674px) {
    .Card {
        width: 80vw;
    }
  }