
.RegisterTEXT{
    text-align: center;
    color: #000000;
}

.RegisterContainer {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
}
.RegisterBox {
    width: 400px;
    height: 360px;
    padding-top: 30px !important;
    background-color: rgba(255, 255, 255, 0.8) !important;
    border-radius: 9px !important;
}

.RegisterForm {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    align-items: center;
    justify-content: center;
}

.RegisterInput {
    width: 75%;
    margin-top: 6px !important;
}

.RegisterButtonContainer{
    width: 100%;
    height: 100%;
    /* border: 1px solid black; */
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    margin-top: 5px;
    align-items: center;
}

.RegisterButtonContainer span {
    font-weight: bold;
}

.RegisterButton{
    background-color: rgb(118, 227, 247) !important;
    margin-top: 20px !important;
}