@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100&display=swap);
body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  /* background-image: url(./images/bg.jpg);
  height: 100vh;
  background-repeat: no-repeat;
  background-size: cover; */
}


.AppBar{
    background-color: rgb(1, 205, 212) !important;
}

.Logo {
	background-image: url(data:image/png;base64,);
	background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
	height: 80px;
	width: 200px;
}

.ButtonsContainer{
    width: 100%;
    display: flex;
    justify-content: flex-end;
}

.HeaderButton{
    color: #ffffff;
    margin: 10px;
}

.PaperContainer {
    height: 50vh;
    width: 50vh;
    border: forestgreen 1px solid;
}
.LoginTEXT {
    text-align: center;
    color: #000000;
    font-family: "Fira Sans Extra Condensed", sans-serif;
}
.LoginTEXT h1 {
    font-family: 'Roboto', sans-serif;
    font-size: 2.5rem;
}

.Alert {
    width: 50%;
    margin: 10px auto;
}

.LoginContainer {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
}
.LoginBox {
    width: 400px;
    height: 220px;
    background-color: rgba(255, 255, 255, 0.8) !important;
    border-radius: 9px !important;
}

.LoginForm {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    align-items: center;
    justify-content: center;
}
.LoginForm .LoginInput {
    width: 80%;
    margin: 5px;
    /* border: 1px #000 solid !important; */
}
.LoginButtonContainer {
    width: 100%;
    /* border: 1px solid black; */
    display: flex;
    justify-content: space-evenly;
    margin-top: 20px;

}

.LoginButtonContainer span {
    font-weight: bold;
    font-family: 'Roboto', sans-serif;
}

.LoginButtonContainer .Button {
    background-color: rgb(118, 227, 247) !important;
}


.RegisterTEXT{
    text-align: center;
    color: #000000;
}

.RegisterContainer {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
}
.RegisterBox {
    width: 400px;
    height: 360px;
    padding-top: 30px !important;
    background-color: rgba(255, 255, 255, 0.8) !important;
    border-radius: 9px !important;
}

.RegisterForm {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    align-items: center;
    justify-content: center;
}

.RegisterInput {
    width: 75%;
    margin-top: 6px !important;
}

.RegisterButtonContainer{
    width: 100%;
    height: 100%;
    /* border: 1px solid black; */
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    margin-top: 5px;
    align-items: center;
}

.RegisterButtonContainer span {
    font-weight: bold;
}

.RegisterButton{
    background-color: rgb(118, 227, 247) !important;
    margin-top: 20px !important;
}
.Card {
    width: 400px;
    margin-top: 25px;
    /* border: 2px forestgreen solid; */
    margin-left: 10px;
    margin-right: 10px;
}
.CardImage {
    width: 100%;
    height: 140px;
}

.CardImage:hover {
    transform: scale(1.2);
    transition: 1s ease;
    text-align: center;
    /* height: 100%; */
}


.MuiCardContent-root {
    padding-bottom: 5px !important;
}

.CardContent {
    display: flex;
    flex-direction: column;
    /* padding: 10px 0 0 20px !important; */
}

.VacationName {
    font-size: 27px;
    font-weight: bold;
}

@media only screen and (max-width: 1125px) {
    .Card {
        width: 43vw;
    }
}

@media only screen and (max-width: 674px) {
    .Card {
        width: 80vw;
    }
  }
.CardsContainer{
    width: 100% !important;
}

.EditAndRemoveIcons .MuiSvgIcon-root {
    color: rgb(79, 131, 155) !important;
}
.AddVacationContainer{
    width: 400px;
    height: 260px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 15px;
    padding-left: 10px;
    padding-right: 10px;
}

.Main {
    margin-top: 22px;
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.Main:hover {
    background-color: rgb(214, 245, 242);
}

.ControlPointIcon {
    font-size: 50px !important;
    color: rgb(79, 131, 155) !important;
}


@media only screen and (max-width: 1125px) {
    .AddVacationContainer{
        width: 43vw;
    }
}

@media only screen and (max-width: 674px) {
    .AddVacationContainer{
        width: 80vw;
    }
  }
.RemoveContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 50px;
}

.RemoveContainer .MuiSvgIcon-root {
    font-size: 40px !important;
}

.RemoveTEXT {
    text-align: center;
    color: #000000;
}

.RemoveBox {
    width: 500px;
    height: 150px;
    padding-top: 15px !important;
    background-color: rgba(255, 255, 255, 0.8) !important;
    border-radius: 9px !important;
}

.RemoveButtonContainer{
    /* border: 1px solid black; */
    width: 50%;
    display: flex;
    justify-content: space-evenly;
    margin-top: 5px;
    /* align-items: center; */
}

.closeButton{
    color: rgb(219, 27, 27) !important;
}

.checkButton{
    color: rgb(34, 177, 34) !important;
}
.Addtitle {
    width: 100%;
    display: flex;
    color: #000000;
    justify-content: center;
}

.Addtitle h2 {
    margin: 10px !important;
}

.AddMainContainer {
    display: flex;
    width: 100%;
    justify-content: space-evenly;
    align-items: center;
}
.AddFromContainer {
    display: flex;
    justify-content: flex-start;
}

.AddBox {
    width: 400px;
    height: 400px;
    padding-top: 15px !important;
    background-color: rgba(255, 255, 255, 0.8) !important;
    border-radius: 9px !important;
}

.AddForm {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    align-items: center;
    justify-content: center;
}

.AddInput {
    width: 75%;
    margin-top: 15px !important;
}

.AddButtonContainer {
    width: 100%;
    height: 100%;
    /* border: 1px solid black; */
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    margin-top: 5px;
    align-items: center;
}

.AddButton {
    background-color: rgb(118, 227, 247) !important;
    margin-top: 10px !important;
}

.AlertContainer {
    display: flex;
    width: 100%;
    height: 65px;
}

.DisplayAddVacationCard {
    height: 250px;
    width: 400px;
    margin-top: 25px;
    border: 2px rgb(65, 141, 155) solid;
    margin-left: 10px;
    margin-right: 10px;
}

.DisplayAddVacationCardImage {
    width: 100%;
    height: 140px;
    border-bottom: 1px rgb(65, 141, 155) solid;
}

@media only screen and (max-width: 850px) {
    .AddMainContainer {
        display: flex;
        width: 100%;
        /* justify-content: space-evenly; */
        align-items: center;
        flex-direction: column-reverse;
    }
    .DisplayAddVacationCard {
        margin-bottom: 20px;
    }
}

